import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import SmallTitle from '../Components/SmallTitle';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import SchoolIcon from '@material-ui/icons/School';
import ResumeItem from '../Components/ResumeItem';

function Resume() {
    const briefcase = <BusinessCenterIcon />
    const school = <SchoolIcon />
    return (
        <ResumeStyled>
            <Title title={' Histórico '} span={'Histórico'} />
            <InnerLayout>
                <div className="small-title">
                    <SmallTitle icon={briefcase} title={'Empresas'} />
                </div>
                <div className="resume-content">
                    <ResumeItem 
                        year={'2015 - 2021'} 
                        title={' Implementador Do Sistema SYSPDV Server'}
                        subTitle={'SuplePet'}
                        text={'Na SuplePet, sou responsável por todas as atividades do Sistemas De Varejo SYSPDV. Que é um Sistema do Grupo Casas Magalhães,  com ele temos controle de todo estoque da loja e monitoramos todas as notas Fiscais, assim, como exportações de arquvos fiscais em xml para o contador. '}
                    />
                   
                </div>
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={school} title={'Escolas'} />
                </div>
                <div className="resume-content ">
                    <ResumeItem 
                        year={'2021 - 2023'} 
                        title={'Certified Tech Developer | CTD'}
                        subTitle={' Digital House | Mercado Live '}
                        text={'Após ganhar uma Bolsa Do Mercado Livre para estudar no CTD da Digital House, está sendo uma experiência incrível.'} 
                    />
                    
                </div>
            </InnerLayout>
        </ResumeStyled>    
    )
}

const ResumeStyled = styled.section`
    .small-title{
        padding-bottom: 3rem;
    }
    .u-small-title-margin{
        margin-top: 4rem;
    }

    .resume-content{
        border-left: 2px solid var(--border-color);
    }
`;
export default Resume
