import React from 'react'
import styled from 'styled-components';
import felipe from '../img/Felipe_perfil.jpg';
import PrimaryButton from './PrimaryButton';

function ImageSection() {
    return (
        <ImageSectionStyled>
            <div className="left-content">
                <img src={felipe} alt=""/>
            </div>
            <div className="right-content">
                <h4>Olá, sou <span>Felipe</span></h4>
                <p className="paragraph">
                 Estudo Desenvolvimento Web, Na Digital House. <span>Gosto muito de Front-End</span>
                    
                </p>
                <div className="about-info">
                    <div className="info-title">
                        <p> Nome</p>
                        <p> Idade </p>
                        <p> Idiomas </p>
                        <p>Cidade</p>
                    </div>
                    <div className="info">
                        <p>: Felipe Rodrigues De Melo</p>
                        <p>: 24</p>
                        <p>: Português, Inglês e Espanhol.</p>
                        <p>: Fortaleza-Ce</p>
                    </div>
                </div>
                <a href="https://drive.google.com/file/d/1fXqPgRgbWio-zfpmPcAR9hxO6NKnsSxM/view?usp=sharing">
                       <PrimaryButton title={'Download Cv'} />
                 </a>
            
            </div>
        </ImageSectionStyled>
    )
}


const ImageSectionStyled = styled.div`
    margin-top: 5rem;
    display: flex;
    @media screen and (max-width:1000px){
        flex-direction: column;
        .left-content{
            margin-bottom: 2rem;
        }
    }
    .left-content{
        width: 100%;
        img{
            width: 95%;
            object-fit: cover;
        }
    }
    .right-content{
        width: 100%;
        h4{
            font-size: 2rem;
            color: var(--white-color);
            span{
                font-size: 2rem;
            }
        }
        .paragraph{
            padding: 1rem 0;
        }
        .about-info{
            display: flex;
            padding-bottom: 1.4rem;
            .info-title{
                padding-right: 3rem;
                p{
                    font-weight: 600;
                }
            }
            .info-title, .info{
                p{
                    padding: .3rem 0;
                }
            }
        }
    }
`;
export default ImageSection;
